<template>
    <div id="app">
        <el-menu theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal"
                 @select="handleSelect">
            <el-menu-item index="1">&emsp;By&emsp;W@I@S@E</el-menu-item>
        </el-menu>
        <div>
            <!--left-->
            <div style="margin-top: 60px;margin-left: 10px; float: left; border: 1px; width: 40%">
                <el-input
                        type="textarea"
                        :autosize="{ minRows: 20, maxRows: 20}"
                        placeholder=""
                        v-model="G">
                </el-input>
            </div>
            <el-button style="margin-top: 220px" type="primary" @click="submit">Eliminate</el-button>
            <!--right-->
            <div style="margin-top: 60px;margin-right: 10px; float: right; border: 1px; width: 40%">
                <el-input
                        type="textarea"
                        :autosize="{ minRows: 20, maxRows: 20}"
                        placeholder=""
                        v-model="G_r">
                </el-input>
            </div>
        </div>

    </div>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        data () {
            return {
                G: '',
                G_r: '',
            }
        },

        methods: {
            startHacking () {
                this.$notify({
                    title: 'Shhh',
                    message: 'Just be patient...',
                    duration: 3000
                })
            },
            submit () {
                var self = this
                axios.post('/eliminate', {G: this.G}).then(function (res) {
                    self.$notify({
                        title: 'Success',
                        message: ',',
                        duration: 3000
                    })
                    self.G_r = res.data['G_r']
                }).catch(function (err) {
                    self.$notify({
                        title: 'Failed',
                        message: 'Something Wrong',
                        duration: 3000
                    })
                })
            }
        }
    }
</script>

<style>
    #app {
        font-family: Helvetica, sans-serif;
        text-align: center;
    }
</style>



